@import url(https://fonts.googleapis.com/css2?family=Alex+Brush&family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap);
* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  box-sizing: border-box;
  font-size: 20px;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background: #fff7ea;
}
.container {
  min-width: 1500px;
  max-width: 1500px;
  margin: 0 auto;
  font-family: "Nunito Sans", sans-serif;
}
@media (max-width: 1520px) {
  .container {
    min-width: 100%;
    padding: 20px;
  }
}

nav {
  display: flex;
  max-width: 1500px;
  min-width: 1500px;
  padding: 25px 10px 40px 10px;
  margin-bottom: 25px;
  justify-content: space-between;
  position: relative;
  align-items: center;
  border-bottom-right-radius: 7px;
  border-bottom-left-radius: 7px;
  margin: 0 auto;
}
nav::after {
  content: "";
  position: absolute;
  height: 0.6px;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgb(201, 201, 201);
}
nav .logo {
  flex: 1 1;
  display: flex;
  align-items: center;
  font-size: 2rem;
}
nav .navLinks {
  flex: 2 1;
}
nav .navLinks {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  
}
nav .navLinks .links {
  display: flex;
  grid-gap: 25px;
  gap: 25px;
 
}
nav .navLinks .links a {
  text-decoration: none;
  font-weight: 300;
  letter-spacing: 1.4px;
  font-size: 24px;
}
nav .navLinks .links a:hover {
  transition: 0.3s;
  cursor: pointer;
}
nav .hamburger {
  display: none;
}
nav svg {
  font-size: 1.75rem;
}
@media (max-width: 1520px) {
  nav {
    min-width: 100%;
    padding: 25px 20px 40px 20px;
  }
}
@media (max-width: 1100px) {
  nav {
    z-index: 20;
    position: unset;
    background: #ffffff63;
    padding: 10px 30px;
    
  }
  nav::after {
    content: "";
    display: none;
  }
  nav .hamburger {
    display: flex;
  }
  nav .navLinks {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: -100%;
    background: #a2783a;
    width: 100%;
    left: 0;
    padding: 15px 7.5px;
    z-index: -1;
    transition: 0.3s;
    
  }
  nav .showmenu {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 67px;
    background: #a2783a;
    left: 0;
    padding: 15px 7.5px;
    transition: 0.3s;
    margin: 0 15px;
    width: 96.7vw;
    margin-right: 30px;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    z-index: 2;
  }
  nav .showmenu .links {
    flex-direction: column;
    text-align: center;
  
  }
  nav .navLinks .links a {
    color: #fff;
  }
}
@media (max-width: 800px) {
  nav .showmenu {
    width: 96vw;
  }
}
@media (max-width: 650px) {
  nav .showmenu {
    margin: 0;
    width: 100%;
    border-radius: 0;
  }
}

.hero {
  position: relative;
  height: 800px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero img {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
}
.hero .item {
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
  background: #fff;
  position: relative;
  padding: 50px 20px;
}
.hero .item h3 {
  font-family: "Alex Brush", cursive;
  color: #a2783a;
  font-weight: 300;
  font-size: 28px;
}
.hero .item div {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
}
.hero .item div h1 {
  font-size: 36px;
  font-weight: 300;
  letter-spacing: 4px;
}
.hero .item div p {
  color: gray;
  font-size: 16px;
}
.hero .item div a {
  padding: 10px 30px;
  color: #fff;
  background: #000;
  width: -moz-fit-content;
  width: fit-content;
  cursor: default;
}
@media (max-width: 620px) {
  .hero {
    padding: 0 20px;
  }
  .hero .item {
    width: 100%;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
}
.contact .banner {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}
.contact .banner .item {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
  border: 1px solid #a2783a;
  padding: 15px 0;
  border-radius: 7px;
}
.contact .banner .item h4 {
  font-weight: 300;
  font-family: "Alex Brush", cursive;
  font-size: 2rem;
}
.contact .banner .item p {
  color: gray;
}
.contact .banner:last-child .item {
  border: none;
}
.contact .banner:last-child .item form {
  width: 100%;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}
.contact .banner:last-child .item form div {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}
.contact .banner:last-child .item form div input {
  flex: 1 1;
}
.contact .banner:last-child .item form h2 {
  font-size: 32px;
  font-weight: 300;
  letter-spacing: 3px;
}
.contact .banner:last-child .item form input,
.contact .banner:last-child .item form textarea {
  padding: 8px;
  background: transparent;
  border: 1px solid #a2783a;
  border-radius: 7px;
}
.contact .banner:last-child .item form button {
  padding: 10px 60px;
  font-size: 24px;
  color: #fff;
  background: #a2783a;
  font-weight: 300;
  font-family: "Alex Brush", cursive;
  border: none;
  width: -moz-fit-content;
  width: fit-content;
}
.contact .banner:last-child .item form input:focus,
.contact .banner:last-child .item form textarea:focus {
  outline: none;
}
@media (max-width: 830px) {
  .contact .banner {
    flex-direction: column;
  }
}
@media (max-width: 640px) {
  .contact .banner:last-child .item form {
    padding: 0;
  }
  .contact .banner:last-child .item form div {
    flex-direction: column;
  }
}

footer {
  padding: 50px 20px;
  background: #000;
  display: flex;
  justify-content: center;
  color: #fff;
}
footer .banner {
  max-width: 750px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer .banner .title {
  text-align: center;
}
footer .banner .title h1 {
  font-size: 40px;
  font-family: "Cormorant Garamond", serif;
  letter-spacing: 3px;
  font-weight: 400;
}
footer .banner .title p {
  color: #a2783a;
  font-family: "Alex Brush", cursive;
  font-size: 24px;
}
footer .banner .tag {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  margin: 20px 0;
}
footer .banner .tag div {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}
footer .banner .tag div input {
  flex: 1 1;
  border-radius: 8px;
  padding: 6px;
  background: transparent;
  border: 1px solid #fff7ea;
}
footer .banner .tag div input:focus {
  outline: none;
}
footer .banner .tag p {
  text-align: center;
}
footer .banner .tag div button {
  background: #fff7ea;
  border: 1px solid #fff7ea;
  padding: 7px 15px;
  border-radius: 8px;
}
@media (max-width: 500px) {
  footer .banner .tag div {
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;
  }
}
.about {
  padding-top: 75px;
  padding-bottom: 75px;
}
.about h4 {
  color: #a2783a;
  font-family: "Alex Brush", cursive;
  font-weight: 200;
  font-size: 24px;
  margin-bottom: 20px;
}
.about h2 {
  color: #000;
  font-weight: 200;
  font-size: 32px;
  margin-bottom: 20px;
  letter-spacing: 4px;
}
.about p {
  line-height: 36px;
  letter-spacing: 2px;
  word-spacing: 6px;
}
.services {
  padding-top: 75px;
  padding-bottom: 75px;
}
.services h2 {
  color: #000;
  font-weight: 200;
  font-size: 32px;
  margin-bottom: 20px;
  letter-spacing: 4px;
}
.services .banner {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 20px;
}
.services .banner .item {
  display: flex;
  position: relative;
  height: 300px;
  overflow: hidden;
}
.services .banner .item img {
  width: 100%;
  height: 100%;
}
.services .banner .item h3 {
  position: absolute;
  background: #fff7ea;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  font-weight: 300;
  color: #a2783a;
  bottom: -200%;
  transition: 0.3s;
}
.services .banner .item:hover h3 {
  bottom: 0;
  transition: 0.3s;
}
@media (max-width: 1280px) {
  .services .banner {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
  }
}
.carousel-container {
  width: 80%;
  margin: 20px auto;
}

.carousel .legend {
  background: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 18px;
  padding: 10px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}
nav .navLinks .links a {
  text-decoration: none;
  font-weight: 300;
  letter-spacing: 1.4px;
  font-size: 24px;
  color: black; /* Changed color to black */
}

nav .navLinks .links a:hover {
  color: #a2783a; /* Optional: Change hover color if needed */
  transition: 0.3s;
  cursor: pointer;
}
nav {
  /* Existing styles */
  padding: 15px 10px 20px 10px; /* Reduced top padding */
  /* Other styles remain unchanged */
}

.slide-container {
  /* Add margin-top if you want extra control */
  margin-top: 10px; /* Adjust this value to your liking */
}

.footer {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    font-family: Arial, sans-serif;
    background-color: black;
}

.footer-section {
    flex: 1 1;
    margin: 0 15px;
}

.footer-section h4 {
    font-size: 16px;
    margin-bottom: 10px;
    font-weight: bold;
}

.footer-section ul {
    list-style: none;
    padding: 0;
}

.footer-section ul li {
    margin-bottom: 8px;
    color: white;
    cursor: pointer;
}

.app-buttons img {
    width: 120px;
    margin-right: 10px;
    cursor: pointer;
}

.social-icons {
    display: flex;
    grid-gap: 25px;
    gap: 25px;
    margin-top: 10px;
}

.social-icons i {
    font-size: 18px;
    color:white;
    cursor: pointer;
}

/* Adjust footer layout for smaller screens */
@media (max-width: 768px) {
    .footer {
        flex-direction: column;
    }

    .footer-section {
        margin-bottom: 20px;
    }

    .app-buttons img {
        width: 100px;
    }
}
.slide-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 700px; /* Increased height */
    width:100%;  /* Increased width */
    margin: 50px auto; /* Centered with spacing above and below */
    overflow: hidden;
    position: relative;
    border-radius: 15px; /* Slightly larger rounded corners */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Stronger shadow for depth */
    background-color: #f5f5f5; /* Light background to complement image loading */
}

.slide-image {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the image fills the container properly */
    transition: transform 1s ease-in-out, opacity 1s ease-in-out; /* Smooth transform and fade transitions */
    transform: scale(1.05); /* Adds a slight zoom effect on each slide */
}

.slide-image:active {
    transform: scale(1); /* Reset zoom on click for interactivity */
}

:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #646cff;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}

